table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 5px;
}
tbody tr:nth-child(odd) {
  background-color: #f9f9f9;
}
th {
  text-align: left;
  border: 1px solid #ddd;
  border-bottom: 3px solid #ddd;
  font-family: 'Glyphicons Halflings';
  position: relative;
  cursor: pointer;
}
th::after {
  content: '';
  position: absolute;
  top: 12px;
  right: 8px;
  display: block;
  opacity: 0.2;
  font-size: 0.7em;
}
th.sortAsc::after {
  content: '\25B2';
  opacity: 0.8;
}
th.sortDesc::after {
  content: '\25BC';
  opacity: 0.8;
}
td {
  border: 1px solid #ddd;
}
td,
th, a {
  padding: 8px;
}

button {
  width: 100px;
  background-color: #44c767;
  border: 1px solid #18ab29;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  padding: 8px 8px;
  text-transform: uppercase;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  margin: 0;
}
button:hover {
  background-color: #5cbf2a;
}
button:active {
  position: relative;
}
textarea {
  width: 500px;
}
